import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap'
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Medication } from "../../../../common/interfaces";
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from "rxjs";
import { GlobalState } from '../../../../global.state';
import { MasterRepo } from '../../../../common/repositories';
import { AuthService } from "../../../../common/services/permission/authService.service";
@Component({
    selector: 'addMedication',
    templateUrl: './AddMedication.component.html',
    providers: [],
    styles: [`
        input{
            color: black;
        }
    `],
})

export class AddMedication implements OnInit, OnDestroy {
    @ViewChild('childModal') childModal: ModalDirective;
    DialogMessage: string = "Saving data please wait ..."
    mode: string = "add";
    viewMode = false;
    modeTitle: string = '';
    medication: Medication = <Medication>{};
    initialTextReadOnly: boolean = false;
    model = {
        //MEDICATIONID: '',
        DESCRIPTION: '',
        REMARKS:''
    }
    private returnUrl: string;
    rategroup: Array<any> = [];
    form: FormGroup;
    private subcriptions: Array<Subscription> = [];
    constructor(private _authService: AuthService, private router: Router, private activatedRoute: ActivatedRoute, private fb: FormBuilder, private masterRepo: MasterRepo, private state: GlobalState) {
        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
    }
    ngOnInit() {
        let self = this;
        this.form = this.fb.group({
            MEDICATIONID: [''],
            DESCRIPTION: [''],
            REMARKS:['']
        });
        if (this.mode == "edit") {
            if (!!this.activatedRoute.snapshot.params['id']) {
                let id = this.activatedRoute.snapshot.params['id'];
                let loadedValue = this.masterRepo._medicationList.filter(r => r.MEDICATIONID == id)[0];
                if (loadedValue != null) {
                    this.form.setValue({ MEDICATIONID: id, DESCRIPTION: loadedValue.DESCRIPTION, REMARKS: loadedValue.REMARKS });
                }
            }
        }
    }

    disabled() {
        if (this.viewMode == true) {
            return "#EBEBE4";
        } else {
            return "";
        }
    }

    changeToArray(data) {
        //console.log(data);
        if (data) {
            let retData: Array<any> = [];
            retData.concat([], data);
            return retData;
        }
        return [];

    }

    onSave() {
        //validate before Saving
        this.DialogMessage = "Saving Data please wait..."
        this.childModal.show();
        this.onsubmit();
    }
    hideChildModal() {
        this.childModal.hide();
    }
    onsubmit() {
        try {
            let medication: Medication = this.form.value;
            //console.log(medication);
            let sub = this.masterRepo.postmaster(this.mode, medication, "/SaveMedication")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        //Displaying dialog message for save with timer of 1 secs
                        this.DialogMessage = "Data Saved Successfully"
                        setTimeout(() => {
                            this.childModal.hide();
                            this.router.navigate([this.returnUrl]);
                        }, 1000)
                    }
                    else {
                        this.DialogMessage = data.result;
                        setTimeout(() => {
                            this.childModal.hide();
                        }, 3000)
                    }
                },
                error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }
    onCancel() {
        //console.log(this.returnUrl)
        this.router.navigate([this.returnUrl]);
    }
    ngOnDestroy() {
        //if(this.subitSubscription)
        //  this.subitSubscription.unsubscribe();
        this.subcriptions.forEach(subs => {
            subs.unsubscribe();

        });
    }
}