import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap'
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Branch, Room } from "../../../../common/interfaces";
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from "rxjs";
import { GlobalState } from '../../../../../app/global.state';
import { MasterRepo } from '../../../../common/repositories';
import { AuthService } from "./../../../../common/services/permission/authService.service";
@Component({
    selector: 'addRoom',
    templateUrl: './AddRoom.component.html',
    providers: [],
    styles: [`
        input{
            color: black;
        }
        .status{ 
            color: black;
            float: left;
            width: 20px;
            margin-left: 15px;
            margin-top: 12px;
        }
    `],
})

export class AddRoom implements OnInit, OnDestroy {
    @ViewChild('childModal') childModal: ModalDirective;
    DialogMessage: string = "Saving data please wait ..."
    mode: string = "add";
    viewMode = false;
    modeTitle: string = '';
    room: Room = <Room>{};
    initialTextReadOnly: boolean = false;
    branches:Branch[] = [];
    private returnUrl: string;
    rategroup: Array<any> = [];
    form: FormGroup;
    private subcriptions: Array<Subscription> = [];
    constructor(private _authService: AuthService, private router: Router, private activatedRoute: ActivatedRoute, private fb: FormBuilder, private masterRepo: MasterRepo, private state: GlobalState) {
        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
    }
    ngOnInit() {
        let self = this;
        this.form = this.fb.group({
            ROOMNO: ['']
            , DESCRIPTION: ['']
            , CAPACITY: [0]
            , branch:[<Branch>{}]
            , IsActive: [true]
            , StatusRemarks: ['']
        });
        this.masterRepo.getBranchList().subscribe(
            (response: Branch[]) => {
                this.branches = response;                
                if (this.mode == "edit") {
                    if (!!this.activatedRoute.snapshot.params['id']) {
                        let id = this.activatedRoute.snapshot.params['id'];
                        let loadedValue = this.masterRepo._roomList.filter(r => r.ROOMNO == id)[0];
                        if (loadedValue != null) {
                            var branch = {} 
                            if(loadedValue.branch)
                               branch = this.branches.find(x=>x.branchId==loadedValue.branch.branchId)
                            this.form.patchValue({ 
                                ROOMNO: loadedValue.ROOMNO, 
                                DESCRIPTION: loadedValue.DESCRIPTION, 
                                CAPACITY: loadedValue.CAPACITY,
                                IsActive: loadedValue.IsActive,
                                StatusRemarks: loadedValue.StatusRemarks,
                                branch: branch });
                        }
                    }
                }
            },
            (err) => {
                this.masterRepo.handleWebError(err);
            }
          );
    }

    disabled() {
        if (this.viewMode == true) {
            return "#EBEBE4";
        } else {
            return "";
        }
    }

    changeToArray(data) {
        //console.log(data);
        if (data) {
            let retData: Array<any> = [];
            retData.concat([], data);
            return retData;
        }
        return [];

    }

    onSave() {
        //validate before Saving
        this.DialogMessage = "Saving Data please wait..."
        this.childModal.show();
        this.onsubmit();
    }
    hideChildModal() {
        this.childModal.hide();
    }
    onsubmit() {
        try {
            console.log('Room', this.form.value);
            let room: Room = this.form.value;
            //console.log(room);
            let sub = this.masterRepo.postmaster(this.mode, room, "/saveRoom")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        //Displaying dialog message for save with timer of 1 secs
                        this.DialogMessage = "Data Saved Successfully"
                        setTimeout(() => {
                            this.childModal.hide();
                            this.router.navigate([this.returnUrl]);
                        }, 1000)
                    }
                    else {
                        this.DialogMessage = data.result;
                        setTimeout(() => {
                            this.childModal.hide();
                        }, 3000)
                    }
                },
                error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }
    onCancel() {
        //console.log(this.returnUrl)
        this.router.navigate([this.returnUrl]);
    }
    ngOnDestroy() {
        //if(this.subitSubscription)
        //  this.subitSubscription.unsubscribe();
        this.subcriptions.forEach(subs => {
            subs.unsubscribe();

        });
    }
}