import {Routes,RouterModule,CanActivate} from '@angular/router';
import {Masters} from './masters.component';
import {CanActivateTeam} from '../../common/services/permission/guard.service';
import {EmployeeList} from './components/Employee/EmployeeList.component';
import {AddEmployee} from './components/Employee/AddEmployee.component';
import {CustomerList} from './components/Customer/CustomerList.component';
import {AddCustomer} from './components/Customer/AddCustomer.component';
import {RoomList} from './components/Rooms/RoomList.component';
import {AddRoom} from './components/Rooms/AddRoom.component';
import {ServiceList} from './components/Services/ServiceList.component';
import {AddService} from './components/Services/AddService.component';
import {SeriesList} from "./components/Series/SeriesList.component";
import{AddSeries} from "./components/Series/AddSeries.component";
import { AddSeriesSales } from './components/SeriesSales/AddSeriesSales.component';
import { SeriesSalesList } from './components/SeriesSales/SeriesSalesList.component';
import { AddDisease } from './components/Disease/AddDisease.component';
import { DiseaseList } from './components/Disease/DiseaseList.component';
import { AddMedication } from './components/Medication/AddMedication.component';
import { MedicationList } from './components/Medication/MedicationList.component';
import { HolidayEntryComponent } from './components/HolidayEntry/holidayEntry.component';
import { AddHolidayComponent } from './components/HolidayEntry/addHoliday.component';
import { PrescriptionList } from './components/Prescription/PrescriptionList.component';
import { AddPrescription } from './components/Prescription/AddPrescription.component';
const routes:Routes =[
    {
        path:'',
        component: Masters,
        children:[
            {path:'emp',component:EmployeeList,canActivate:[CanActivateTeam]},
             {path:'cus',component:CustomerList,canActivate:[CanActivateTeam]},      
              {path:'room',component:RoomList,canActivate:[CanActivateTeam]},      
               {path:'ser',component:ServiceList,canActivate:[CanActivateTeam]},            
               {path:'series',component:SeriesList,canActivate:[CanActivateTeam]},
               {path:'seriesSales',component:SeriesSalesList,canActivate:[CanActivateTeam]},
            {path:'emp/detail-emp',component:AddEmployee,canActivate:[CanActivateTeam]}, 
             {path:'room/detail-room',component:AddRoom,canActivate:[CanActivateTeam]}, 
              {path:'cus/detail-cus',component:AddCustomer,canActivate:[CanActivateTeam]}, 
               {path:'ser/detail-ser',component:AddService,canActivate:[CanActivateTeam]},      
               {path:'series/detail-series',component:AddSeries,canActivate:[CanActivateTeam]}, 
               {path:'seriesSales/detail-seriesSales',component:AddSeriesSales,canActivate:[CanActivateTeam]}, 
               {path:'disease/detail-disease',component:AddDisease,canActivate:[CanActivateTeam]}, 
               {path:'disease',component:DiseaseList,canActivate:[CanActivateTeam]},
               {path:'medication/detail-medication',component:AddMedication,canActivate:[CanActivateTeam]}, 
               {path:'medication',component:MedicationList,canActivate:[CanActivateTeam]},
               {path:'prescription/detail-prescription',component:AddPrescription,canActivate:[CanActivateTeam]},
               {path:'prescription',component:PrescriptionList,canActivate:[CanActivateTeam]},
               {path:'holiday', component:HolidayEntryComponent, canActivate:[CanActivateTeam]},
               {path:'holiday/detail-holiday', component: AddHolidayComponent, canActivate:[CanActivateTeam]},
               ]
        
    }
];

export const routing = RouterModule.forChild(routes);