import { Component, OnInit, forwardRef, Inject, Input, ElementRef } from "@angular/core";
import { FormBuilder, FormControl } from "@angular/forms";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { HolidayModel } from "../interfaces";
import { MasterRepo } from "../repositories";
import { DisableDateService } from "../services/disable-date-picker/disable-date-picker.service";
import { nepalidFormat } from "../../data/disabeDates";
// import { disableDate } from '../services/disable-date-picker/disableDate.model';
import { DatePipe } from "@angular/common";

declare var jQuery;
// declare var nepalidFormat

@Component({
  selector: "nepali-date-picker",
  templateUrl: "nepali-date-picker.component.html",
  styleUrls: ["nepali-date-picker.component.scss"],
  providers: [
    DatePipe,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => NepaliDatePickerComponent),
      multi: true,
    },
  ],
})
export class NepaliDatePickerComponent implements OnInit, ControlValueAccessor {
  // public nepalidFormat: any[] = [];

  private onChange: (value: string[]) => void = () => {};
  private onTouch: any = () => {};
  public disableBefore = "";
  @Input() id: string = "";
  @Input() label: string = "";
  disableDates: any[] = [];
  formatDates: string[] = [];
  holiday: HolidayModel;
  toDateBS: string;

  public dateControl = new FormControl();
  constructor(
    private elementRef: ElementRef,
    private disableDateService: DisableDateService,
    private masterRepo: MasterRepo,
    private datePipe: DatePipe,
    private masterService: MasterRepo
  ) {
  }

  changeToDate(value, format: string) {
    this.toDateBS = this.masterService.toBSDate(value);
    return this.toDateBS;
  }

  ngOnInit() {
    this.isNullOrEmpty(this.id);
  }

  ngAfterViewInit() {
    var self = this;

    jQuery(document).ready(function () {
      jQuery(document).on("click", `#${self.id}`, function () {
        document.getElementById(`${self.id}Picker`).focus();
      });
    });

    jQuery(document).ready(function () {
      jQuery(document).on("focus", `#${self.id}Picker`, function () {
        jQuery(`#${self.id}Picker`).nepaliDatePicker({
          onChange: function () {
            self.dateControl.setValue(jQuery(`#${self.id}Picker`).val());            
            self.propagateChange(self.dateControl.value);
          },
        });
      });
    });
  }

  writeValue = (obj: any): void => {
    this.dateControl.setValue(obj);
  };

  registerOnChange = (_fn: any): void => {
    this.onChange = _fn;
  };

  registerOnTouched = (_fn: any): void => {
    this.onTouch = _fn;
  };

  private propagateChange = (value: any) => {
    this.onChange(value);
    this.onTouch(value);
    this.elementRef.nativeElement.dispatchEvent(
      new CustomEvent("change", { detail: { value: value }, bubbles: true })
    );
  };

  isNullOrEmpty(val) {
    if (typeof val !== "string") {
      throw Error("Invalid Type for Id");
    }

    if (val == "" || val == null || val == undefined) {
      throw Error("Invalid Id");
    }
  }
}
