import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap'
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Series, Service } from "../../../../common/interfaces";
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from "rxjs";
import { GlobalState } from '../../../../../app/global.state';
import { MasterRepo } from '../../../../common/repositories';
import { AuthService } from "./../../../../common/services/permission/authService.service";
@Component({
    selector: 'addSeries',
    templateUrl: './AddSeries.component.html',
    providers: [],
    styles: [`
        input{
            color: black;
        }
    `],
})

export class AddSeries implements OnInit, OnDestroy {
    @ViewChild('childModal') childModal: ModalDirective;
    DialogMessage: string = "Saving data please wait ..."
    mode: string = "add";
    viewMode = false;
    modeTitle: string = '';
    series: Series = <Series>{};
    initialTextReadOnly: boolean = false;
    ItemList: any[];
    ServiceList: Service[];
    Item:any={};
    service:any=<any>{};
    private returnUrl: string;
    rategroup: Array<any> = [];
    form: FormGroup;
    private subcriptions: Array<Subscription> = [];
    constructor(private _authService: AuthService, private router: Router, private activatedRoute: ActivatedRoute, private fb: FormBuilder, private masterRepo: MasterRepo, private state: GlobalState) {
        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
    }
    ngOnInit() {
        let self = this;
        this.form = this.fb.group({            
            MappingCode:[''],
            ServiceCount: [''],
            ValidityPeriod: [''],
            ServiceId: ['']
        });
        if (this.mode == "edit") {
            if (!!this.activatedRoute.snapshot.params['id']) {
                let id = this.activatedRoute.snapshot.params['id'];

                let loadedValue = this.masterRepo._seriesList.filter(r => r.MappingCode == id)[0];
                if (loadedValue != null) {
                    this.form.setValue({
                        MappingCode:loadedValue.MappingCode,
                        ServiceCount: loadedValue.ServiceCount,
                        ValidityPeriod: loadedValue.ValidityPeriod,
                        ServiceId: loadedValue.ServiceId,
                    })
                }
            }
        }
        let _iList:any[]= [];
    this.masterRepo.getSeriesItemList().subscribe((data) => {
            _iList.push(<Series>data);
        },
        Error => console.log(Error),
        () => {
            //console.log({itemlist: _iList});
            this.ItemList = _iList;
        });
        let _serviceList:Service[]= [];
    this.masterRepo.getServiceList().subscribe((data) => {
            _serviceList.push(<Service>data);
        },
        Error => console.log(Error),
        () => {
            //console.log({servicelist: _serviceList});
            this.ServiceList = _serviceList;
        });


    }

    disabled() {
        if (this.viewMode == true) {
            return "#EBEBE4";
        } else {
            return "";
        }
    }

    changeToArray(data) {
        //console.log(data);
        if (data) {
            let retData: Array<any> = [];
            retData.concat([], data);
            return retData;
        }
        return [];

    }

    onSave() {
        //validate before Saving
        this.DialogMessage = "Saving Data please wait..."
        this.childModal.show();
        this.onsubmit();
    }
    hideChildModal() {
        this.childModal.hide();
    }
    onsubmit() {
        try {            
            let ser: Series = this.form.value;
            ser.ServiceName = this.ServiceList.find(x=>x.SERVICEID == ser.ServiceId).DESCRIPTION;
            ser.SeriesName = this.ItemList.find(x=>x.MCODE == ser.MappingCode).DESCA;
            //console.log(this.service.DESCRIPTION);
            let sub = this.masterRepo.postmaster(this.mode, ser, "/saveSeries")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        //Displaying dialog message for save with timer of 1 secs
                        this.DialogMessage = "Data Saved Successfully"
                        setTimeout(() => {
                            this.childModal.hide();
                            this.router.navigate([this.returnUrl]);
                        }, 1000)
                    }
                    else {
                        this.DialogMessage = data.result;
                        setTimeout(() => {
                            this.childModal.hide();
                        }, 3000)
                    }
                },
                error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }
    onCancel() {
        //console.log(this.returnUrl)
        this.router.navigate([this.returnUrl]);
    }
    ngOnDestroy() {
        //if(this.subitSubscription)
        //  this.subitSubscription.unsubscribe();
        this.subcriptions.forEach(subs => {
            subs.unsubscribe();

        });
    }
}